import React from 'react';
import { MyInfoInput } from '../../../../styles/common/input';
import { InfoBox, InfoLabel } from '../../../../styles/mypage/myinfo';

function EmailInput ({register}) {
  return (
    <InfoBox>
      <InfoLabel>  </InfoLabel>
      <MyInfoInput 
        {...register("email")}
        type="email"
        placeholder=''
      />
    </InfoBox>
  );
}

export default React.memo(EmailInput);