import styled from "styled-components";

export const InfoBox = styled.div`
  width: 90%;
  height: 30%;
  display: flex;
  flex-direction: column;
  gap: 5px;
`;

export const InfoLabel = styled.label`
  font-size: 1em;
  font-weight: bold;
  padding-left: 3px;
  margin-bottom: 3px;
`;